.header, .footer{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    position: relative;
    background-color: var(--mainColor);    
    gap: 10px;
    color: var(--secondaryColor);
}
.header :link:first-child{
    margin-right: auto;
}
.header button{
    background-color: var(--mainColor);
    color: var(--tertiaryColor);
    font-size: 20px;
    padding: 10px 60px;
    border-radius: 5px;
    margin: 10px 0px;
    cursor: pointer;
}
.header button:hover{
    background-color: var(--quaternaryColor);
    color: var(--secondaryColor);
}

.selectedButton{
    background-color: red;
}

.footer{
    position:absolute;
    bottom:0;
    width:100%;
}

.icon:hover{
    color: var(--quaternaryColor);
    cursor: pointer;

}
